import {
	friendlyDate,
	cutString
} from "@/common/util.js";
import api from "../../api/api.js";

export default {
	data() {
		return {
			msgs: [
				{
					id: 1,
					chatUserHeadImg: "/static/images/img_1.jpg",
					chatUserName: "小马达",
					latestMsgContent: "你什么时候回家？",
					notReadMsgNum: 1,
					latestMsgTime: "7:00"
				},
				{
					id: 2,
					chatUserHeadImg: "/static/images/img_2.jpg",
					chatUserName: "真有钱",
					latestMsgContent: "你需要钱吗？我借钱给你，5千够吗？",
					notReadMsgNum: 1,
					latestMsgTime: "14:00"
				},
				{
					id: 3,
					chatUserHeadImg: "/static/images/img_3.jpg",
					chatUserName: "好厉害",
					latestMsgContent: "我真的太菜了，还是你厉害。",
					notReadMsgNum: 0,
					latestMsgTime: "19:00"
				},
				{
					id: 4,
					chatUserHeadImg: "/static/images/img_4.jpg",
					chatUserName: "真滴烦",
					latestMsgContent: "哇哇哇——啊啊啊——好气啊——气死我了——啊啊啊——",
					notReadMsgNum: 100,
					latestMsgTime: "5-3"
				},
			]
		}
	},
	onLoad: function() {
		for (var msgItem of this.msgs) {
			// 裁剪过长的消息内容
			msgItem.latestMsgContent = cutString(msgItem.latestMsgContent, 15);
			// 隐藏过多的未读消息
			msgItem.notReadMsgNum = (msgItem.notReadMsgNum > 99 ? '99+' : ''+msgItem.notReadMsgNum);
		}
		
		// 测试请求模块
		// GET
		var url = api.urls.login;
		var data = {
				phone: "13078901232",
				password: "12345abc"
			};
		api.req.get(url, data, this, function(that, res){
			console.log(res);
		});
		// POST
		var url = api.urls.sendMessage;
		var data = {
				messageContent: "hi",
				acceept: 1 // 接受消息的用户ID
			};
		api.req.post(url, data, this, function(that, res){
			console.log(res);
		});
		// PUT
		var url = api.urls.updateUserInfo;
		var data = {
				name: "hehe",
				phone: "13078901232",
				password: "12345abc"
			};
		api.req.put(url, data, this, function(that, res){
			console.log(res);
		});
		// DELETE
		var url = api.urls.deleteCollect;
		var data = {
				collectId: 1
			};
		api.req.del(url, data, this, function(that, res){
			console.log(res);
		});
		uni.request({
			url: '/schoolhelp/schoolhelp-1.0.1/post/type/' + 1,
			method: 'GET',
			success: (result) => {
				console.log(result);
			},
		});
	},
	methods: {
		showMsgDetail: function(e){
			let msgId = e.currentTarget.id;
			// let msgIndex = e.currentTarget.dataset.index;
			// console.log(msgId, msgIndex);
			uni.navigateTo({
				url: "message-detail/message-detail?msgId=" + msgId
			});
		}
	}
}
